@use "global" as *;

.top-contact {
	position: relative;


}
.top-contact::before {
	@include mq(md) {
		content:"";
		position: absolute; /*  */
    top: 0; /* 0px */
		background-image: url('../images/bonotu-pc.jpg'); /*  */
    width: 100%;   /*  */
    height: rem(500);  /* 500 */
    background-size: cover; /*  */
    background-position: center; /*  */
		z-index: 1; /* z-index */
	}
}
.top-contact-layout {
	margin-top: rem(40);
	@include mq(md) {
			margin-top: rem(120);
	}
}
.top-contact__container {
	margin-inline:auto;
	width:100%;
	margin-top: rem(40);
	text-align: center;
	@include mq(md) {
		margin-top: rem(60);
	}
}
.top-contact__body {
	margin-inline:auto;
	width: 100%;
	@include mq(md) {
			width: max-content;
	}
}

.top-contact__text {
	text-align: left;
	font-size: rem(20);
			font-family: $base-font-family;

		}


.top-contact__button {
	display: inline-block;
	margin-top: rem(40);
  padding-bottom: rem(4);

  @include mq(md) {
    margin-top: rem(60);
  }

}
